.intro {
    padding-top: 6vh;
}

.intro__container {
    padding-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #f5f5f5;
}

.intro__title {
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: .05rem;
    padding: .5rem 0;
    text-align: center;
}

.intro__job {
    font-size: 3rem;
    padding-bottom: .5rem;
}

.intro__btn-icon {
    padding-right: .5rem;
}

.intro__btn-downloadcv {
    border: 1px solid var(--white);
    color: var(--black);
    background-color: var(--white);
    text-decoration: none;
    padding: 1rem 1.5rem;
    font-size: 1.5rem;
    font-weight: 700;
    margin: 1rem 0;
    border-radius: 1.25rem;
    transition: all ease .5s;
    box-shadow: var(--white) 0 0 .5rem;
    letter-spacing: 1.5px;
}

.intro__btn-downloadcv:hover {
    background-color: var(--blue);
    border-color: var(--blue);
    color: var(--black);
    letter-spacing: 3px;
    box-shadow: var(--blue) 0px 7px 29px 0px;
}

.intro__btn-downloadcv:active {
    letter-spacing: 3px;
    background-color: var(--blue);
    color: var(--black);
    box-shadow: var(--blue) 0px 0px 0px 0px;
    transform: translateY(10px);
    transition: 100ms;
}

.intro__links-container {
    padding: .5rem 0;
    margin-left: 2rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

@media(max-width:1200px) {
    .intro__title {
        font-size: 3rem;
        letter-spacing: .05rem;
        padding: .25rem 0;
    }

    .intro__job {
        font-size: 2rem;
        padding-bottom: .5rem;
    }
}

@media(max-width:800px) {
    .intro__title {
        font-size: 2.5rem;
        letter-spacing: .05rem;
        padding: .25rem 0;
    }

    .intro__job {
        font-size: 1.5rem;
        padding-bottom: .5rem;
    }
}

@media(max-width:500px) {
    .intro__title {
        font-size: 2.5rem;
        letter-spacing: .05rem;
        padding: .25rem 0;
    }

    .intro__job {
        font-size: 1.5rem;
        padding-bottom: .5rem;
    }

}