.aboutme {
    margin: 15rem 0;
}

.aboutme__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--white);
    padding-top: 4rem;
}

.aboutme__title {
    font-size: 4.5rem;
    font-weight: 900;
    text-transform: uppercase;
}

.aboutme__text {
    width: 80%;
    font-size: 1.75rem;
    letter-spacing: .125rem;
    text-align: center;
    line-height: 1.5;
    padding-top: 1.5rem;
}

@media(max-width:800px) {
    .aboutme__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }

    .aboutme__text {
        width: 100%;
        font-size: 1.25rem;

    }
}

@media(max-width:500px) {
    .aboutme__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }

    .aboutme__text {
        width: 100%;
        font-size: 1.25rem;

    }

}