.projects {
    padding: 15rem 0;
}

.projects__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.projectcards__container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.projects__title {
    font-size: 4.5rem;
    color: var(--white);
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    padding-bottom: 2rem;
}

.projects__subtitle {
    color: var(--white);
    font-weight: 700;
    text-align: center;
    padding-bottom: 4rem;
}

@media(max-width:800px) {
    .projects__title {
        font-size: 2.5rem;
        padding-bottom: 1.5rem;
    }

    .projects__subtitle {
        padding-bottom: 3rem;
    }
}