.footer {
    padding: 15rem 0;
}

.footer__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__title {
    font-size: 4.5rem;
    color: var(--white);
    font-weight: 900;
    text-transform: uppercase;
}

.footer__contact-container {
    padding-top: 4rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.contact {
    display: flex;
    flex-direction: column;
    color: var(--white);
    gap: 1rem;
    text-decoration: none;
}

.contact__title {
    font-size: 1.75rem;
    font-weight: 700;
}

.contact__text-link {
    color: var(--white);
    text-decoration: none;
    transition: all .5s;
}

.contact__text-link:hover {
    color: var(--blue);
    transform: scale(1.3);
}

.contact__links-icon {
    display: flex;
    gap: .5rem;
    color: var(--white);
    text-decoration: none;
    font-size: 1.25rem;
    transition: all .5s;
}

.contact__links-icon:hover {
    color: var(--blue);
    transform: scale(1.3);
}

@media(max-width:1200px) {}

@media(max-width:1000px) {
    .footer__contact-container {
        flex-direction: column;
        align-items: flex-start;
        gap: 3rem;
    }

    .footer__title {
        font-size: 2.5rem;
    }
}

@media(max-width:500px) {}