.project__card {
    width: 90%;
    background-color: var(--white);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    perspective: 1000px;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.project__card-img {
    width: 100%;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.project__card:hover {
    transform: scale(1.05);
    box-shadow: 0 .5rem 1rem rgba(255, 255, 255, 0.712);
}

.project__card-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 1.75rem;
    box-sizing: border-box;
    background-color: #f2f2f2;
    transform: rotateX(-90deg);
    transform-origin: bottom;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    display: flex;
    flex-direction: column;
}

.project__card:hover .project__card-content {
    transform: rotateX(0deg);
}

.project__card:hover .project__card-img {
    scale: 0;
}

.project__card-title {
    font-size: 1.5rem;
    font-weight: 700;
    padding-top: 1rem;
}

.project__card-description {
    height: 100%;
    line-height: 1.5rem;
    padding: 1rem;
    font-weight: 500;
}

.project__card-btn {
    background-color: var(--blue);
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.25rem;
    font-weight: 700;
    padding: .5rem .75rem;
    border-radius: .5rem;
    transition: all .5s;
    justify-self: flex-end;
    align-self: flex-end;
    color: black;
}

.project__card-btn:hover {
    background-color: var(--black);
    color: var(--white);
    cursor: pointer;
}