.skills {
    padding: 15rem 0;
}

.skills__container {
    display: flex;
    flex-direction: column;
}

.skills__title {
    font-size: 4.5rem;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    color: var(--white);
}

.skillcards__container {
    padding-top: 4rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

@media(max-width:800px) {
    .skills__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }

    .skillcards__container {
        justify-content: center;
    }
}

@media(max-width:500px) {
    .skills__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }

    .skillcards__container {
        justify-content: center;
    }

}