.header {
    height: 6vh;
    background-color: var(--blue);
    z-index: 5;
    position: fixed;
    width: 100%;
}

.header__container {
    display: flex;
    align-items: center;
    height: 100%;
}

.header__nav {
    display: flex;
    flex-direction: column;
    background-color: var(--black);
    color: var(--blue);
    width: 13rem;
    z-index: 6;
}

.header__nav-item {
    padding-block: 1rem;
    padding-left: 1rem;
    font-size: 1.25rem;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--blue);
    transition: all .3s;
}

.header__nav-item:hover {
    color: var(--black);
    cursor: pointer;
    background-color: var(--blue);
}