.btn__menu {
    width: 150px;
    height: 50px;
    border-radius: 5px;
    border: none;
    transition: all 0.5s ease-in-out;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 600;
    display: flex;
    align-items: center;
    background: var(--blue);
    color: var(--black);
    transition: all .3s;
}

.btn__menu:hover {
    /* box-shadow: 0 0 20px 0px #2e2e2e3a; */
    cursor: pointer;
}

.btn__menu-icon {
    position: absolute;
    height: 30px;
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s;
    color: var(--black);
}

.btn__menu-text {
    transform: translateX(55px);
}

.btn__menu:hover .btn__menu-icon {
    width: 175px;
}

.btn__menu:hover .btn__menu-text {
    transition: all 0.5s;
    opacity: 0;
}

.btn__menu:focus {
    outline: none;
}

.btn__menu:active .btn__menu-icon {
    transform: scale(0.85);
}