:root {
  --black: #0A0A0A;
  --black2: #181818;
  --white: #FAFAFA;
  --white-transparent: #FAFAFA80;
  --white-background: #e6e6e6;
  --blue: #e6af2e;
  --blue2: #086a83;
  --pink: #ee2fd4;
  --pink2: #9e148c;
  --transparent: #0a0a0aee;
  --font: 'Roboto', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #040f16;
}

.container {
  width: 90%;
  margin: 0 auto;
  max-width: 1600px;
}