.book {
    position: relative;
    border-radius: 10px;
    width: 15rem;
    height: 18rem;
    background-color: var(--white);
    -webkit-box-shadow: 1px 1px 12px var(--black);
    box-shadow: 1px 1px 12px var(--black);
    -webkit-transform: preserve-3d;
    -ms-transform: preserve-3d;
    transform: preserve-3d;
    -webkit-perspective: 2000px;
    perspective: 2000px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: var(--black);
}

.book__text {
    margin-left: 2.5rem;
    font-weight: 500;
}

.cover {
    top: 0;
    position: absolute;
    background-color: var(--blue);
    width: 100%;
    height: 100%;
    border-radius: 10px;
    cursor: pointer;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform-origin: 0;
    -ms-transform-origin: 0;
    transform-origin: 0;
    -webkit-box-shadow: 1px 1px 12px var(--black);
    box-shadow: 1px 1px 12px var(--black);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border: 2px solid var(--black);
    box-shadow: 2px 2px 15px var(--black) inset;
}


.book:hover .cover {
    -webkit-transform: rotateY(-80deg);
    -ms-transform: rotateY(-80deg);
    transform: rotateY(-80deg);
}

.cover__title {
    font-size: 1.5rem;
    font-weight: 900;
}

p {
    font-size: 1.25rem;
    font-weight: bolder;
    font-weight: 300;
}

@media(max-width:800px) {
    .skills__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }
}

@media(max-width:500px) {
    .skills__title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
    }

}