.linkicon {
    text-decoration: none;
    color: var(--white);
    font-weight: 700;
}

.linkicon__icon {
    font-size: 4rem;
    margin-right: 2rem;
    color: var(--white);
    transition: all .5s;
}

.linkicon:hover {
    color: var(--blue);
}

.linkicon:hover .linkicon__icon {
    scale: 1.1;
    color: var(--blue);
}